import './scss/styles.scss';
import WebStoreApi from './components/WebStoreApi';
import { API_URL, CDN_URL } from './utils/constants';
import { EventEmitter } from './components/base/events';
import { AppState } from './components/AppData';
import { Page } from './components/Page';
import { cloneTemplate, ensureElement } from './utils/utils';
import { Modal } from './components/common/Modal';
import { Basket } from './components/common/Basket';
import { ProductCatalog, CatalogProdactsView, BasketProduct } from './components/ProductCatalog';
import { Order } from './components/Order';
import { Success } from './components/common/Success';
import { Contacts } from './components/Contacts';
// Ð’ÑÐµ ÑˆÐ°Ð±Ð»Ð¾Ð½Ñ‹
const cardCatalogTemplate = ensureElement('#card-catalog');
const cardPreviewTemplate = ensureElement('#card-preview');
const cardBasketTemplate = ensureElement('#card-basket');
const basketTemlate = ensureElement('#basket');
const orderTemplate = ensureElement('#order');
const contactsTemplate = ensureElement('#contacts');
const successTemplate = ensureElement('#success');
const events = new EventEmitter();
const api = new WebStoreApi(CDN_URL, API_URL);
const appData = new AppState({}, events);
// Ð“Ð»Ð¾Ð±Ð°Ð»ÑŒÐ½Ñ‹Ðµ ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ñ‹
const page = new Page(document.body, events);
const modal = new Modal(ensureElement('#modal-container'), events);
//
const basket = new Basket(cloneTemplate(basketTemlate), events);
const contacts = new Contacts(cloneTemplate(contactsTemplate), events);
const order = new Order(cloneTemplate(orderTemplate), events);
const success = new Success(cloneTemplate(successTemplate), {
    onClick: () => {
        modal.close();
    },
});
// Ð§Ñ‚Ð¾Ð±Ñ‹ Ð¼Ð¾Ð½Ð¸Ñ‚Ð¾Ñ€Ð¸Ñ‚ÑŒ Ð²ÑÐµ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ, Ð´Ð»Ñ Ð¾Ñ‚Ð»Ð°Ð´ÐºÐ¸
events.onAll(({ eventName, data }) => {
    console.log(eventName, data);
});
// Get Ð·Ð°Ð¿Ñ€Ð¾Ñ Ð½Ð° Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ Ð²ÑÐµÑ… Ð¿Ñ€Ð¾Ð´ÑƒÐºÑ‚Ð¾Ð² Ð¼Ð°Ð³Ð°Ð·Ð¸Ð½Ð°.
api
    .getProductList()
    .then((items) => {
    appData.setCatalog(items);
})
    .catch(console.error);
//ÐžÐ±Ñ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°ÐµÐ¼ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ðµ, ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ðµ Ð²Ñ‹Ð²Ð¾Ð´Ð¸Ñ‚(Ð¾Ñ‚Ð¾Ð±Ñ€Ð°Ð¶Ð°ÐµÑ‚) ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸ Ð¿Ñ€Ð¾Ð´ÑƒÐºÑ‚Ð¾Ð² Ð½Ð° Ð³Ð»Ð°Ð²Ð½Ð¾Ð¹ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ðµ.
events.on('catalog:changed', () => {
    page.catalog = appData.catalog.map((item) => {
        const product = new ProductCatalog(cloneTemplate(cardCatalogTemplate), {
            onClick: () => events.emit('card:select', item),
        });
        return product.render({
            title: item.title,
            image: item.image,
            category: item.category,
            price: item.price,
        });
    });
});
//Ð‘Ð»Ð¾ÐºÐ¸Ñ€ÑƒÐµÐ¼ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÑƒ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹ ÐµÑÐ»Ð¸ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð° Ð¼Ð¾Ð´Ð°Ð»ÐºÐ°
events.on('modal:open', () => {
    page.locked = true;
});
// ... Ð¸ Ñ€Ð°Ð·Ð±Ð»Ð¾ÐºÐ¸Ñ€ÑƒÐµÐ¼
events.on('modal:close', () => {
    page.locked = false;
});
events.on('card:select', (item) => {
    const productInBasket = appData.inBasket(item.id);
    const product = new CatalogProdactsView(cloneTemplate(cardPreviewTemplate), {
        onClick: () => {
            if (productInBasket) {
                events.emit('card:deleteFromBasket', item);
            }
            else {
                events.emit('card:toBasket', item);
            }
        },
    });
    modal.render({
        content: product.render({
            title: item.title,
            image: item.image,
            category: item.category,
            description: item.description,
            price: item.price,
            buttonText: productInBasket ? 'Ð£Ð´Ð°Ð»Ð¸Ñ‚ÑŒ Ð¸Ð· ÐºÐ¾Ñ€Ð·Ð¸Ð½Ñ‹' : 'Ð”Ð¾Ð±Ð°Ð²Ð¸Ñ‚ÑŒ Ð² ÐºÐ¾Ñ€Ð·Ð¸Ð½Ñƒ',
        }),
    });
});
events.on('basket:changed', () => {
    basket.total = appData.getTotalBasket();
    basket.list = appData.basket.map((item, index) => {
        const basketProduct = new BasketProduct(cloneTemplate(cardBasketTemplate), {
            onClick: () => {
                events.emit('card:deleteFromBasket', item);
            },
        });
        return basketProduct.render({
            title: item.title,
            price: item.price,
            index: index + 1,
        });
    });
});
events.on('card:toBasket', (item) => {
    appData.addBasket(item);
    modal.close();
});
events.on('card:deleteFromBasket', (item) => {
    appData.removeBasket(item);
});
events.on('Ñounter:change', () => {
    page.counter = appData.getBasketCount();
});
events.on('basket:open', () => {
    modal.render({
        content: basket.render(),
    });
});
events.on('order:open', () => {
    modal.render({
        content: order.render({
            payment: appData.order.payment,
            address: appData.order.address,
            valid: appData.validateOrder(),
            errors: [],
        }),
    });
});
events.on('orderErrors:change', (errors) => {
    const { payment, address } = errors;
    order.valid = !payment && !address;
    order.errors = Object.values({ payment, address })
        .filter((i) => !!i)
        .join('; ');
});
events.on('input:change', (data) => {
    appData.setOrderField(data.field, data.value);
});
events.on('order:submit', () => {
    modal.render({
        content: contacts.render({
            email: appData.order.email,
            phone: appData.order.phone,
            valid: appData.validateContacts(),
            errors: [],
        }),
    });
});
events.on('contactsErrors:change', (errors) => {
    const { email, phone } = errors;
    contacts.valid = !email && !phone;
    contacts.errors = Object.values({ email, phone })
        .filter((i) => !!i)
        .join('; ');
});
events.on('contacts:submit', () => {
    const total = appData.getTotalBasket();
    const items = appData.getProductIDs();
    api
        .orderProducts(appData.order, total, items)
        .then((res) => {
        appData.clearBasket();
        appData.clearOrder();
        events.emit('Ñounter:change');
        modal.render({
            content: success.render({
                total: res.total,
            }),
        });
    })
        .catch((err) => {
        console.error(err);
    });
});
