import { Form } from './common/Form';
import { ensureElement } from '../utils/utils';
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this._onlineButton = container.querySelector('button[name="card"]');
        this._cashButton = container.querySelector('button[name="cash"]');
        this._address = ensureElement('input', this.container);
        this._onlineButton.addEventListener('click', () => {
            this.payment = 'card';
            this.onInputChange('payment', 'card');
        });
        this._cashButton.addEventListener('click', () => {
            this.payment = 'cash';
            this.onInputChange('payment', 'cash');
        });
    }
    set address(value) {
        this._address.value = value;
    }
    set payment(value) {
        this._onlineButton.classList.toggle('button_alt-active', value === 'card');
        this._cashButton.classList.toggle('button_alt-active', value === 'cash');
    }
}
