import { Form } from './common/Form';
export class Contacts extends Form {
    constructor(container, events) {
        super(container, events);
        this._inputPhone = container.querySelector('input[name="phone"]');
        this._inputEmail = container.querySelector('input[name="email"]');
    }
    set phone(value) {
        if (this._inputPhone) {
            this._inputPhone.value = value;
        }
    }
    set email(value) {
        if (this._inputEmail) {
            this._inputEmail.value = value;
        }
    }
}
