import { Model } from './base/Model';
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = {
            address: '',
            payment: 'card',
            email: '',
            phone: '',
        };
        this.formErrors = {};
    }
    setCatalog(items) {
        this.catalog = items;
        this.emitChanges('catalog:changed');
    }
    getBasketCount() {
        return this.basket.length;
    }
    getTotalBasket() {
        return this.basket.reduce((sum, item) => {
            return sum + item.price;
        }, 0);
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('card:select', item);
    }
    addBasket(item) {
        this.basket.push(item);
        this.emitChanges('basket:changed');
        this.emitChanges('Ñounter:change');
    }
    inBasket(id) {
        return this.basket.some((item) => item.id === id);
    }
    getProductIDs() {
        return this.basket.map((item) => item.id);
    }
    removeBasket(item) {
        this.basket = this.basket.filter((id) => id != item);
        this.emitChanges('basket:changed');
        this.emitChanges('Ñounter:change');
    }
    clearBasket() {
        this.basket = [];
        this.emitChanges('basket:changed');
    }
    setOrderField(field, value) {
        this.order[field] = value;
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
        if (this.validateContacts()) {
            this.events.emit('contacts:ready', this.order);
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.order.payment) {
            errors.payment = 'Ð’Ñ‹Ð±ÐµÑ€Ð¸Ñ‚Ðµ ÑÐ¿Ð¾ÑÐ¾Ð± Ð¾Ð¿Ð»Ð°Ñ‚Ñ‹';
        }
        if (!this.order.address) {
            errors.address = 'Ð£ÐºÐ°Ð¶Ð¸Ñ‚Ðµ Ð°Ð´Ñ€ÐµÑ';
        }
        this.formErrors = errors;
        this.events.emit('orderErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    clearOrder() {
        return (this.order = {
            payment: '',
            address: '',
            email: '',
            phone: '',
        });
    }
    validateContacts() {
        const errors = {};
        if (!this.order.email) {
            errors.email = 'Ð£ÐºÐ°Ð¶Ð¸Ñ‚Ðµ email';
        }
        if (!this.order.phone) {
            errors.phone = 'Ð£ÐºÐ°Ð¶Ð¸Ñ‚Ðµ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½';
        }
        this.formErrors = errors;
        this.events.emit('contactsErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
}
